.container {
  max-width: 350px;
  margin: 0 auto;
}
.wrapper {
  margin: 2rem 1rem;
  padding: 20px;
  background-color: hsl(216, 50%, 16%);
  border-radius: 15px;
  box-shadow: 0px 25px 50px rgba(0, 0, 0, 0.0952917);
}
.img-top {
  border-radius: 12px;
  overflow: hidden;
  position: relative;
}
.img-top img {
  width: 100%;
  display: block;
}
.img-overlay {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: hsla(178, 100%, 50%, 0.5);
  display: none;
}
.img-overlay img {
  position: absolute;
  width: 50px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.img-top:hover .img-overlay {
  display: block;
}
.card-text a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  font-size: 21px;
  padding: 20px 0;
  display: inline-block;
}
.card-text a:hover {
  color: hsl(178, 100%, 50%);
}
p {
  color: hsl(215, 51%, 70%);
}
.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px 0;
}
.ethereum,
.days {
  display: flex;
  align-items: center;
}
.ethereum p {
  color: hsl(178, 100%, 50%);
  font-weight: 600;
  padding-left: 7px;
}
.days p {
  padding-left: 7px;
}
hr {
  border-color: hsl(215, 32%, 27%);
}
.author {
  display: flex;
  align-items: center;
  padding-top: 13px;
  padding-bottom: 14px;
}
.author img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 2px solid #fff;
  margin-right: 15px;
}
.author p a.name {
  color: #fff;
  font-size: 16px;
  text-decoration: none;
}
.author p a.name:hover {
  color: hsl(178, 100%, 50%);
}
